// -----------------------------------------------------------------------------
// TYPOGRAPHY
// -----------------------------------------------------------------------------


/*
 * Using 5 sizes based on perfect fifth scale (2:3) using 20px as base size. 
 * Scale generated using https://www.modularscale.com/
*/ 
 
$font-size-xxl: 101.25px;
$font-size-xl: 67.5px;
$font-size-l: 45px;
$font-size-m: 30px;
$font-size-s: 20px;
$font-size-xs: 16px;

 

h1 {
    font-size: $font-size-xl;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;

    @include media-query(hd) {
        font-size: $font-size-xxl;
        letter-spacing: -3px;
    }
}


h2 {
    font-size: $font-size-l;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;

    @include media-query(hd) {
        font-size: $font-size-xl;
        letter-spacing: -2px;
    }
}


h3 {
    font-size: $font-size-m;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;
    margin-top: 25px;

    @include media-query(hd) {
        font-size: $font-size-l;
        letter-spacing: -2px;
        margin-top: 50px;
    }
}



h4 {
    font-size: $font-size-s;
    font-weight: 700;
    margin-top: 10px;
    line-height: 1.1;

    @include media-query(hd) {
        font-size: $font-size-m;
    }
}



p {
    font-size: $font-size-xs;
    line-height: 1.5;
    margin-top: 15px;

    @include media-query(hd) {
        font-size: $font-size-s;
    }
}


em {
    font-style: italic;
}
 





